* {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-color: #080808;
    color: #f0f0f0;
    height: 100%; /* added */
    background-image: url(../src/assets/homepage_v1.png);
}

@media only screen and (max-width: 600px) {

    .header-text {
        margin-top: 65%;
        font-size: 20px;
    }

    .header-text h1 {
        font-size: 40px;
        margin-top: 20px;
    }

    .logo {
        display: none;
    }

    .sub-title {
        font-size: 40px;
    }

    .about-col-1 {
        display: none;
        flex-basis: 100%;
    }

    .about-col-2 {
        flex-basis: 100%;
        font-size: 14px;
    }

    .tab-links {
        font-size: 17px;
        margin-right: 10px;
        white-space: normal;
    }

    .contact-left, .contact-right {
        flex-basis: 100%;
    }

    .tab-contents ul li {
        font-size: 18px;
    }

    ul {
        display: inline-block;
        list-style: none;
        margin: 20px auto;
        word-spacing: 3em;
        font-size: 5px;
        text-align: center;
    }

}

